.product-comparison {
	margin-top: 60px;
	position: relative;
	&__table-wrapper {
		-webkit-overflow-scrolling: touch;
		overflow-x: scroll;
		overflow-y: auto;
		table.table {
			tr {
		    height: 50px;
			}
			tr:nth-child(even) {
				background: #f2f2f2;
			}
			th, td {
				vertical-align: middle;
				border: 1px solid #ccc;
		    border-top-color: transparent;
		    border-bottom-color: transparent;
		    padding: 1rem 2rem;
		    font-size: 14px;
		    color: #4d4d4d;
		    &.chinese {
		    	font-size: 15px;
		    }
			}
			tr:not(:first-child) .chinese {
				font-weight: 200;
			}
		}
		tr.attribute_names {
			th {
				border-top-color: transparent;
				&.chinese:not(:first-child) {
					font-weight: 200;
				}
			}
		}
	}
	.table {
		margin-bottom: 0px;
		&.copied_header {
			-webkit-overflow-scrolling: touch; // add this to make itself on the very top, or the table content will overlay on this.
			z-index: 10000;
			background: aliceblue;
			position: absolute;
			left: -7px; // 為了蓋住左邊不知道為啥會透光
			box-shadow: 7px 2px 8px -4px #ddd;
			tr th:first-child {
				border-top-color: transparent;
			}
			tr th {
				border-left-color: transparent;
			}
		}
	}
}


